@import url(https://fonts.googleapis.com/css2?family=Zen+Maru+Gothic:wght@300&display=swap);


.card {
    background-color: white;
    border-radius: 5px;
    height: calc(100vh - 80px);
    margin: 10px;
    overflow: auto;
}

.link_linkedin {
    font-size: 30px;
    color: #0a66c2;
    margin-right: 15px;
}

.link_github {
    font-size: 30px;
    color: #1b1e23;
    margin-right: 15px;
}

.link_twiter {
    font-size: 30px;
    color: #1d9bf0;
    margin-right: 15px;
}

.link_mail {
    font-size: 30px;
    color: #e34133;
    margin-right: 15px;
}

.link_instagram {
    font-size: 30px;
    color: #c734a0;
    margin-right: 15px;

}
#header_nav {
    display: flex;
    flex-direction: row;
}

.navClose {
    background-image: linear-gradient(to bottom, #79a6e9, #6294e4, #4a82de, #326fd7, #135dcf);
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    height: 100vh;
    overflow: hidden;
    transition: all 500ms;
    width: 60px;
}

.navOpen {
    background-image: linear-gradient(to bottom, #79a6e9, #6294e4, #4a82de, #326fd7, #135dcf);
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    height: 100vh;
    overflow: hidden;
    transition: all 500ms;
    width: 350px;
}

.header{
    align-items: center;
    background-color:white;
    display: flex;
    height: 60px;
    justify-content: space-between;
    width: 100%;
    z-index: 100;
}

.title_name {
    display: flex;
    align-items: center;
    flex-direction: row;
}

.icon_name {
    color: white;
    margin-left: 10px;
}

.name {
    color: white;
    font-size: 15px;
    margin-left: 5px;
    transition: all 300ms;
    white-space: nowrap;
}

.name_close {
    color: transparent;
    font-size: 15px;
    margin-left: 5px;
    transition: all 300ms;
    white-space: nowrap;
}

.hr_header {
    width: 80%;
}

.item_link {
    display: flex;
    align-items: center;
    border-radius: 5px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 5px 5px 5px 5px;
    padding: 5px 0px 5px 0px;
}

.item_link_title {
    display: flex;
    align-items: center;
}

.icon_name {
    color: white;
    font-size: 35px;
}

.icon {
    color: white;
    font-size: 30px;
    margin: 0px 0px 0px 10px;
}

.link_label {
    color: white;
    font-size: 15px;
    margin-left: 15px;
    transition: all 300ms;
    white-space: nowrap;
}

.link_label_close {
    color: transparent;
    font-size: 15px;
    margin-left: 15px;
    transition: all 300ms;
    white-space: nowrap;
}

.link {
    -webkit-text-decoration-line: none;
            text-decoration-line: none;
    margin-top: 10px;
}

.item_link:hover{
    background-color: #0c57c7;
}

.titleHeader {
    color: white;
    font-size: 25px;
    -webkit-margin-after: 0;
            margin-block-end: 0;
    -webkit-margin-before: 0;
            margin-block-start: 0;
}

.handle_button {
    background-color: transparent;
    border: none;
    cursor: pointer;
    height: 50px;
    margin-left: 10px;
    width: 50px;
}

.div_portfolio {
    font-family: 'Balsamiq Sans', cursive;
    font-size: 20px;
    margin-right: 20px;
    text-align: center;
    width: 100%;
}

.header_body {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.menu_icon_close {
    color: #1c407b;
    font-size: 25px;
    transform: rotate(540deg);
    transition: all 500ms;
}

.menu_icon_open {
    color: #1c407b;
    font-size: 25px;
    transition: all 500ms;
}

.active {
    background-color: #0c57c7;
    border-radius: 10px;
}

.img_my_photo {
    width: 35px;
    height: 35px;
    margin: 10px 12px 0px 12px;
    border-radius: 100%;
}

.arrow_icon {
    font-size: 12px;
    color: white;
    margin-right: 15px;
}
.img_certi {
    height: 200px;
    width: 280px;
}

.certificates {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    margin-left: 10px;
    padding-top: 10px;
}

.card_certificate {
    align-items: center;
    background: #ffffff;
    border-radius: 21px;
    box-shadow:  6px 6px 6px #bdbdbd, -6px -6px 6px #ffffff;
    display: flex;
    flex-direction: column;
    height: 250px;
    justify-content: flex-start;
    margin-left: 3%;
    margin-right: 3%;
    margin-top: 10px;
    overflow: hidden;
    width: 250px;
}

.title_download {
    display: flex;
    justify-content: space-between;
    margin: 0px 10px 0px 10px;
}

.div_download {
    align-items: center;
    border-radius: 100%;
    display: flex;
    height: 35px;
    justify-content: center;
    width: 35px;
}

.download_icon {
    color: rgb(68, 68, 68);
    font-size: 20px;
}

.div_download:hover {
    background-color: #d1d1d185;
}


.div_title {
    align-items: center;
    display: flex;
    justify-content: start;
    margin-left: 25px;
    padding-top: 20px;
}

.icon_title {
    color: #1c407b;
    font-size: 30px;
}

.title_page {
    align-self: center;
    font-size: 25px;
    margin-left: 15px;
}

.hr_title {
    margin: 10px 15px 0px 15px;
}

.card_body {
    margin-left: 25px;
    margin-right: 25px;
    padding-top: 20px;
}

.header_cardBody {
    display: flex;
    flex-direction: row;
}

.header_cardBody_icon {
    color: #1c407b;
}

.contact_linkedin {
    position: absolute;
    bottom: 20px;
}


.date_element {
    color: black;
}

.icon_element {
    display: flex;
    align-self: center;
    align-items: center;
    justify-content: center;
    font-size: 30px;
}

.card_body_projects {
    margin-left: 25px;
    margin-right: 25px;
    padding-top: 20px;
}

.table_projects {
    width: 80%;
    table-layout: fixed;
    border-collapse: collapse;
}

.tr_project {
    border-bottom: 1px solid rgba(177, 177, 177, 0.596);
}

.name_link {
    color: #000000;
    text-decoration: none;
}

.name_link:hover {
    color: rgb(189, 15, 15);
    
}

.repository_icon {
    color: rgb(68, 68, 68);
}

.repository_icon:hover {
    color: rgb(236, 11, 11);
}

.card_body {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.card_resume {
    background: #dbdbdb;
    border-radius: 25px;
    box-shadow:  20px 20px 60px #bababa, -20px -20px 60px #fcfcfc;
    height: 400px;
    overflow: hidden;
    width: 300px;
}

.img_resume {
    height: 350px;
    width: 300px;
}

.card_img {
    align-items: center;
    display: flex;
    height: 90%;
    justify-content: center;
    overflow: hidden;
}

.img {
    background-image: url(/static/media/cv.9e0d7061.PNG);
    background-size: 100%;
    height: 100%;
    overflow: hidden;
    width: 100%;
}

.div_link {
    align-items: center;
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;
    display: flex;
    height: 360px;
    justify-content: center;
    overflow: hidden;
    position: absolute;
    transition: all 300ms;
    width: 300px;
    z-index: 99;
}

.div_link:hover {
    background-color: rgba(100, 100, 100, 0.445);
}

.div_link:hover > .link_resume > .link_resume_icon {
    color: #1c407b;
}

.link_resume_icon {
    color: transparent;
    font-size: 50px;
    position: relative;
    transition: all 300ms;
    z-index: 0;
}

.title_card_resume {
    text-align: center;
    z-index: 100;
}

.hr_vertical {
    rotate: 180;
    margin-left: 20px;
    margin-right: 20px;
}

.div_skills {
    width: 70%;
}

.tables {
    display: flex;
    justify-content: flex-start;
}

table {
    font-size: 15px;
    border-collapse: collapse;
    text-align: left;
    width: 300px;
}

.tr_header {
    border-bottom: 1.3px solid rgba(177, 177, 177, 0.596);
}

.tr_skills {
    border-bottom: 1px solid rgba(177, 177, 177, 0.596);
}

.td_skills {
    width: 30%;
}
.socialNetworks {
    display: flex;
    flex-direction: column;
}

.socialNetworks_item {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.div_text_talk {
    width: 70%;
    text-align: left;
}
.card_iten_nav {
    -webkit-backdrop-filter: blur(5px);
            backdrop-filter: blur(5px);
    background-color: rgba(114, 114, 114, 0.219);
    border-radius: 15px;
    height: 28%;
    margin-left: 20px;
    margin-right: 20px;
    text-decoration: none;
    transition: all 500ms;
    width: 28%;
}

.card_iten_nav:hover {
    background-color: rgba(104, 104, 104, 0.171);
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);
}
.card_iten_nav:hover > .card_icon_nav > div > .icon_homeScreen {
    font-size: 60px;
}

.card_iten_nav:hover > .card_title_nav > h2 {
    font-size: 25px;
}

.card_icon_nav {
    align-items: center;
    background-color: white;
    border-radius: 100%;
    border: 1px solid rgba(163, 163, 163, 0.253);
    display: flex;
    height: 100px;
    justify-content: center;
    margin: auto;
    position: relative;
    top: -50px;
    width: 100px;

}

.icon_homeScreen {
    align-self: center;
    color: #1c407b;
    font-size: 50px;
    transition: all 500ms;
}

.card_title_nav {
    bottom: 30px;
    color: white;
    position: relative;
    text-align: center;
}

.card_title_nav > h2 {
    font-size: 20px;
    transition: all 500ms;
}
.card_fondo_home {
    background-color: rgb(255, 255, 255);
    background-image:url(/static/media/imgFondo.11c5acac.png);
    background-position: center center;
    background-size: cover;
    max-width: 100%;
}

.card_body_home {
    align-items: center;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}








body {
  margin: 0;
  font-family: 'Zen Maru Gothic', sans-serif, 'roboto';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f5f5f5;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h2 {
  -webkit-margin-before: 0px;
          margin-block-start: 0px;
  -webkit-margin-after: 0px;
          margin-block-end: 0px;
}

h4 {
  -webkit-margin-before: 0px;
          margin-block-start: 0px;
  -webkit-margin-after: 0px;
          margin-block-end: 0px;
}

h3 {
  -webkit-margin-before: 0px;
          margin-block-start: 0px;
  -webkit-margin-after: 0px;
          margin-block-end: 0px;
}
@media (max-width: 1300px) {
    .card_certificate {
        margin-left: 0%;
    }

    .tables {
        flex-direction: column;
    }

    table {
        margin-bottom: 15px;
    }
}
@media (max-width: 1214px) {
    .header_cardBody {
        display: flex;
        flex-direction: column;
    }

    i.fas.fa-birthday-cake.header_cardBody_icon {
        margin-left: -20px;
    }

    i.far.fa-flag.header_cardBody_icon {
        margin-left: -20px;
    }
}


@media (max-width: 1204px) {
    .certificates {
        justify-content: space-between;
        margin-left: 5px;
    }
    
    .card_certificate {
        height: 240px;
        margin-left: 3%;
        margin-right: 3%;
        margin-top: 10px;
        width: 210px;
    }
}

@media (max-width: 1204px) {
    .card_certificate {
        width: 270px;
    }
}

@media (max-width: 1120px) {
    .card_iten_nav{
        margin-left: 0;
        margin-right: 0;
    }
}

@media (max-width: 1000px) {
    .table_projects {
        width: 100%;
    }
    
    .certificates {
        justify-content: center;
    }
    
    .hr_vertical {
        display: none;
    }
    
    .div_skills {
        display: none;
    }
    
    .div_text_talk {
        width: 70%;
        text-align: left;
        margin-left: 100px;
    }
    
    .contact_linkedin {
        display: none;
    }
}

@media (max-width: 900px) {
    .card_resume {
        height: 300px;
    }
}

@media (max-width: 800px) {
    .div_text_talk {
        display: none;
    }
    .navOpen {
        width: 280px;
    }
    .div_portfolio {
        font-size: 12px;
    }
    .card_iten_nav:hover > .card_title_nav > h2 {
        font-size: 20px;
    }
    .card_title_nav > h2 {
        font-size: 15px;
    }
}


@media (max-width: 700px) {
    .handle_button {
        display: none;
    }
}
@media (max-width: 600px) {
    .th_technology {
        display: none;
    }
    
    .th_date {
        display: none;
    }
    
    .td_technology {
        display: none;
    }
    
    .td_date {
        display: none;
    }
    
    .card_resume {
        width: 230px;
    }
}

@media (max-width: 500px) {
    .name {
        display: none;
    }
    
    .navOpen {
        width: 200px;
    }
    
    .card_title_nav {
        display: none;
    }
    
    .card_iten_nav {
        height: 0;
    }
    
}

@media (max-width: 400px) {

    .card_icon_nav {
        width: 70px;
        height: 70px;   
    }
    .icon_homeScreen {
        font-size: 35px;
    }
    .card_iten_nav:hover > .card_icon_nav > div > .icon_homeScreen {
        font-size: 45px;
    }
}
